button {

    cursor: pointer; 
 }
 
.guess-boxes {
  
  width: 100%;
  max-width: 510px;
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto; /* Add this line to center the component */
}

.rectangle {

  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  background-color: lightgrey; /* Set the background color to aqua */
  width: 99%; /* Adjust the width as needed */
  height: 35px; /* Adjust the height as needed */
  margin-bottom: 5px; /* Add spacing between the rectangles */
}

.animate {
  animation-duration: 0.5s;
  animation-fill-mode: both;
  opacity: 0;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.guess-name{

  flex: .4;
  text-align: left;
  animation-delay: 0.2s;
  animation-name: fadeIn;
  overflow-wrap: break-word;
  
}

.guess-distance{
  text-align: center;
  animation-delay: 0.4s;
  animation-name: fadeIn;
  margin-right: 43px;
}
.guess-direction{
  text-align: center;
  animation-delay: 0.6s;
  animation-name: fadeIn;
  font-size: 25px;
  margin-right: 15px;
}

.rectangle.correct {
  width: 97%;
  border: 2px solid black;
  display: flex;
  align-items: center;
  padding-left: 5px;
  background-color: lightgreen;
  overflow: hidden;
}

.rectangle.incorrect {
  width: 97%;
  border: 2px solid black;
  display: flex;
  align-items: center;
  padding-left: 5px;
  background-color: lightcoral;
  overflow: hidden;
}


.guess-button {

    border: 1px solid black;
    border-radius: 5px;
    background-color: white; /* Set the background color to aqua */
    width: 506px; /* Adjust the width as needed */
    height: 35px; /* Adjust the height as needed */
    margin-bottom: 100px;
  }
.guess-button:hover{

  background-color: lightgray;
}

.guess-button.disabled:hover{

  background-color: white;
  cursor: default;
}

.text-field {
  margin-top: 5px;
  border: 1px solid lightgray;
  border-radius: 4px;
  background-color: white;
  max-width: 500px;
  height: 35px;
  margin-bottom: 5px;
}

/* Media query for mobile devices */
@media (max-width: 480px) {
  .guess-button {
    /* Mobile styles */
    width: 99.5%; /* Adjust the width as needed */
  }

  .text-field {
    /* Mobile styles */
    max-width: 96%; /* Adjust the width as needed */
  }
}
